import portfolio from '../assets/portfolio.jpg'
import restauant from '../assets/restaurant.jpg'
import volunteer from '../assets/volunteer.jpg'
import company from '../assets/Newcompany.jpg'
import { Card,Container,Col,Row, } from 'react-bootstrap'
import './page3.css'

const Page3 = (props)=>{

    const FileOriginal= (props)=>{
        return(
          
            <Card  >
                <Card.Img src={props.image} alt="Card image" />
                    <Card.ImgOverlay>
                        <Card.Title style={{color:'aqua',width:'5em',fontFamily:'Bahnschrift Condensed'}}>{props.title}</Card.Title>
                            
                             <Card.Text style={{color: "aqua" ,width:'8em',fontFamily:'Bahnschrift Condensed',borderStyle:"solid",borderColor:'black'}}>{props.sub}</Card.Text>
                    </Card.ImgOverlay>
            </Card>
        )
    }

    return(
        <div >
      <Container>
        <div style={{position:'absolute',width:'195vh',marginTop:'5vh'}}>
          <h3 className='text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${props.color}`}}>what is your website for</h3>
        </div>
    <Row>
      <Col lg={12}>
        <Row>
      <Col className='  pt-5 ' lg={4} >
        <div className= 'mt-5 pt-5'>
          <FileOriginal image={company} title='Company' sub="customer confidence"></FileOriginal>
            {/* <FileOriginal image={company} title='Company' sub='company identity' ></FileOriginal> */}
        </div>
    </Col>
    <Col className='pt-5 mt-5'>
    <h4 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color: `${props.color}`}}>The kind of website we can make?</h4>
    </Col>
        </Row>
        <Row>
          <Col className='pt-5 offset-lg-2'lg={4}>
          <div className='  mt-5 pt-5'>
            <FileOriginal image={restauant} title='culinary' sub='Your restaurant'></FileOriginal>
          </div>
          </Col>
          <Col className='mt-5 pt-5'>
          <h4 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${props.color}`}}>
          Why a portfolio site matters
          </h4>
          </Col>
        </Row>
        <Row>
          <Col className='pt-5  offset-lg-4' lg={4}>
          <div className=' mt-5 pt-5'>
            <FileOriginal image={volunteer} title='volunteer' sub='your mind'>
            </FileOriginal>
          </div>
          </Col>
          <Col className='mt-5 pt-5 text-end'>
          <h4 className='mt-5 pt-5' style={{fontFamily:'Bahnschrift Condensed',color:`${props.color}`}}>what price trust</h4>
          </Col>
        </Row>
        <Row>
          <Col className='pt-5 ' lg={4}>
          <div className='exc'>
          <FileOriginal image={portfolio} title='in person' sub='more skilled'></FileOriginal>
        </div>
           </Col>
          <Col className='mt-5 pt-5'>
          <h1 className='mt-5 pt-5 text-center' style={{fontFamily:'Bahnschrift Condensed',color:`${props.color}`}}>2023: 5 billion web users/64.4</h1>
          </Col>
          </Row>
         </Col>
    </Row>
    
    
    </Container>
    </div>
   
    )
   
}

export default Page3