import Cards from './cards';
import BackgroundTop from '../assets/Top.mp4'
import logo from "../assets/logo.jpg"
import Background2 from '../assets/background2.jpg'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'animate.css'
// import 'aos/dist/aos.css'
// import Aos from 'aos';
import gambar1 from '../assets/informatika.jpeg'
import Page3 from './page3';
import Land3 from './land3';
// import DomCoba from './Dom';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import gambar2 from '../assets/gambar2.jpg'
import gambar3 from '../assets/gambar3.jpg'
import gambar4 from '../assets/gambar4.jpg'
import Responsive from './responsivePage';
import Trust from './Trust';
import Footer from './footer';




import { Col,Row,Button, Container, Form,Nav,NavDropdown,Navbar } from 'react-bootstrap';

function App2() {

  
   

  function NavScrollExample() {
    const navigate= useNavigate()
    return (
       <Navbar expand="lg" variant='dark' style={{position:'absolute',top:'0',width:'100%'}} className='px-4 pt-4'>
        <Container fluid>
          <Navbar.Brand href="#">
            <img src={logo} alt="Bootstrap" width="80" height="80"/>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '100px' }}
              navbarScroll
            >
              <Nav.Link href="#action1" className='h11'>Home</Nav.Link>
              <Nav.Link href="#action2">Link</Nav.Link>
              <NavDropdown title="Link" id="navbarScrollingDropdown">
                <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action4">
                  Another action
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action5">
                  Something else here
                </NavDropdown.Item>
              </NavDropdown>
              <Nav.Link  onClick={()=>navigate('/coba2')}>
                Link
              </Nav.Link>
            </Nav>
            <Form className="d-flex" >
              <Form.Control 
              style={{backgroundColor:'#31FFF9'}}
                type="search"
                placeholder="Search"
                className="me-2"
                aria-label="Search"
                
                
              />
              <Button variant="outline-info">Search</Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
        );
  }
  
// style="width: 18rem;"

// -----------------------------------THEME DARK AND LIGHT LANDING 3

const [theme,setTheme]=useState({
  displayLight:"none",
  displayNight: "block",
  backgroundColor:'white',
  fontColor:'white',


  Light: ()=>{
    setTheme(data=>{
      return{
        ...data,
        displayLight:'none',
        displayNight:'block',
        backgroundColor:"white"
       
        
      }
    })
  },

  Night:()=>{
    setTheme(data=>{
      return{
        ...data,
        displayLight:'block',
        displayNight:'none',
        backgroundColor:'red'
        
        
      }
    })
  }
})

const navgite= useNavigate()

  return (

    

    <div className="App" >
  

  {/*----------------------------Landing 1----------------------------------------------------- */}
      <div style={{zIndex: "0"}}>
      <video src={BackgroundTop} autoPlay loop muted>
        <h1>Jeremi</h1>
      </video>
      
      <div className='content'>
        <h1 className='h12 animate__animated animate__flash animate__infinite animate__slower'>Iam Jeremi Christian</h1>
        <br/>
        <h6 className='animate__animated  animate__lightSpeedInRight animate__slower '>What gift did you get this Christmas?</h6>
      </div>
      <NavScrollExample></NavScrollExample>
    </div>




{/* -----------------LANDING 2------------------ */}



      <Container   className=' bg mt-0 text-center ' fluid style={{backgroundImage: `url(${Background2})` ,backgroundSize:'cover'}} data-aos="flip-right" data-aos-duration='1000'>
      <div data-aos="flip-up" data-aos-duration='1500'>
      <h1 className='h10 pt-5' style={{fontWeight:"normal",fontFamily:'Bahnschrift Condensed',fontSize:'7.5vh',color:'white'}} data-aos='zoom-out-down' data-aos-duration='2000'>My Skills</h1>
      </div>
    <Container>
      <Row className='mt-3'>
        <Col sm={6}lg={4} className='mt-5 '>
          <div data-aos="zoom-in-down" data-aos-duration='2000'>
          <h5  className='mt-5' style={{fontWeight:"normal",fontFamily:'Bahnschrift Condensed',fontSize:'5vh',color:'white'}}>
          I am a web design specialist who is ready to design your web in a fast and in the best possible way.
          </h5>
          </div>
          
          <Button variant='outline-info' className='mt-3' data-aos='fade-up'
     data-aos-duration="2000" onClick={()=>navgite('/Email')}>ASK MORE</Button>
          
          
        </Col>
        <Col lg={8} sm={6}>
          <Row className='mt-3'>
           
          <Col lg={4} sm={6} className=' h20 offset-lg-3  mt-5' data-aos="zoom-out-rigth">
            <Cards gambar={gambar1} skil='Javascript'></Cards>
          </Col>
            
            
          <Col lg={4} sm={6} className=' h20 mt-5 ' data-aos="zoom-out-left" >
          <Cards gambar={gambar2} skil='UI library'></Cards>
          </Col>
            
            
          <Col lg={4} sm={6} className='h20 offset-lg-3 mt-5' data-aos="zoom-out-right" data-aos-duration='1000'>
          <Cards gambar={gambar3} skil='React Js'></Cards>
          </Col>
          
          <Col lg={4} sm={6} className='h20  mt-5' data-aos="zoom-out-left" data-aos-duration='1000'>
          <Cards gambar={gambar4} skil='Database'></Cards>
          </Col>
            
          </Row>
        
        </Col>
      </Row>
    </Container>
   </Container>


   {/* ------------------LANDING 3----------------------------- */}
    
    <div style={{backgroundColor:`${theme.backgroundColor}`}} >


      {/* -------------------------------------------THEME--------------------------------------- */}
    
    <div style={{marginLeft:'600px',paddingTop:'10px'}}>
    {/* LIGHT */}
    <div style={{position:'absolute',color:'yellow',cursor:'pointer',display:`${theme.displayLight}`}} onClick={()=>theme.Light()} >
      <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor" class="bi bi-brightness-low-fill" viewBox="0 0 16 16">
          <path d="M12 8a4 4 0 1 1-8 0 4 4 0 0 1 8 0M8.5 2.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0m0 11a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0m5-5a.5.5 0 1 1 0-1 .5.5 0 0 1 0 1m-11 0a.5.5 0 1 1 0-1 .5.5 0 0 1 0 1m9.743-4.036a.5.5 0 1 1-.707-.707.5.5 0 0 1 .707.707m-7.779 7.779a.5.5 0 1 1-.707-.707.5.5 0 0 1 .707.707m7.072 0a.5.5 0 1 1 .707-.707.5.5 0 0 1-.707.707M3.757 4.464a.5.5 0 1 1 .707-.707.5.5 0 0 1-.707.707"/>
      </svg>
    </div>

    {/* DARK */}
    <div style={{position:'absolute',color:'aqua',marginLeft:'10px',display:`${theme.displayNight}`,cursor:'pointer'}} onClick={()=>theme.Night()} >
      <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-moon-stars-fill" viewBox="0 0 16 16">
        <path d="M6 .278a.768.768 0 0 1 .08.858 7.208 7.208 0 0 0-.878 3.46c0 4.021 3.278 7.277 7.318 7.277.527 0 1.04-.055 1.533-.16a.787.787 0 0 1 .81.316.733.733 0 0 1-.031.893A8.349 8.349 0 0 1 8.344 16C3.734 16 0 12.286 0 7.71 0 4.266 2.114 1.312 5.124.06A.752.752 0 0 1 6 .278"/>
        <path d="M10.794 3.148a.217.217 0 0 1 .412 0l.387 1.162c.173.518.579.924 1.097 1.097l1.162.387a.217.217 0 0 1 0 .412l-1.162.387a1.734 1.734 0 0 0-1.097 1.097l-.387 1.162a.217.217 0 0 1-.412 0l-.387-1.162A1.734 1.734 0 0 0 9.31 6.593l-1.162-.387a.217.217 0 0 1 0-.412l1.162-.387a1.734 1.734 0 0 0 1.097-1.097l.387-1.162zM13.863.099a.145.145 0 0 1 .274 0l.258.774c.115.346.386.617.732.732l.774.258a.145.145 0 0 1 0 .274l-.774.258a1.156 1.156 0 0 0-.732.732l-.258.774a.145.145 0 0 1-.274 0l-.258-.774a1.156 1.156 0 0 0-.732-.732l-.774-.258a.145.145 0 0 1 0-.274l.774-.258c.346-.115.617-.386.732-.732L13.863.1z"/>
      </svg>
    </div>
    </div>
    <Page3 color='aqua'></Page3>
    </div>
      
    


    {/* -------------------------------LANDING 4------------------------------ */}
    <div>
      <Land3></Land3>
    </div>

    {/* -------------------------LANDING 5--------------------------------- */}
    <div>
      <Responsive/>
    </div>

    {/* -----------------Landing 6-------------------------- */}

    <div>
    <Trust/> 
    </div>


    {/* ----------------------------------Landing 7-------------------------------------- */}


    {/*---------------------------------- Footer----------------------------------------------- */}
    <div>
      {/* footer */}
      <div>
        <Footer></Footer>
      </div>
    </div>




    {/* PERCOBAAN DOM */}
    {/* <div>
      <DomCoba></DomCoba>
    </div> */}
  

   
   

   {/* <Container >
    <Col lg={1}>
    
      <h1 className='rotate-out-center'>Jeremi</h1>
    
    </Col>
  
   </Container> */}

   
      
    </div>

    
           
  );
}

export default App2;
