
import './Cards.css'
import { Card } from 'react-bootstrap'

const Cards = (props)=>{
    return(
      <Card className="h25 bg-dark text-white">
      <Card.Img src={props.gambar} alt="Card image"   />
      <Card.ImgOverlay>
        {/* <Card.Title  className=' h15 ' >JavaScript</Card.Title> */}
        <h3 className='h15 ' style={{color:'aqua'}}>{props.skil}</h3>
         
      </Card.ImgOverlay>
    </Card>
    )
  }

  export default Cards;