import responsive from '../assets/responsive.jpg'
import { Image } from 'react-bootstrap';
import 'animate.css'
const Responsive=()=>{
    return(
        <>
        <div style={{position:'absolute',left:'30vh'}}>
            {/* for title */}
            <div style={{display:'inline-block'}} data-aos="flip-left" data-aos-duration='1000'>
                <h1 style={{fontFamily:'Bahnschrift Condensed',color:'aqua'}}>For everyone to access</h1>
            </div>

            {/* for image  */}
             <div style={{display:'inline-block',position:'relative',left:'10vh'}} data-aos="zoom-in" data-aos-duration='1000'>
                <Image src={responsive} style={{width:'80vh'}}/>
            </div>
            <div style={{position:'relative', width:'20vh',backgroundColor:'white',left:'75vh',top:'50vh'}}>
            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor" class="bi bi-reception-4" viewBox="0 0 16 16">
            <path d="M0 11.5a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1-.5-.5zm4-3a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 .5.5v5a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1-.5-.5zm4-3a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 .5.5v8a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1-.5-.5zm4-3a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 .5.5v11a.5.5 0 0 1-.5.5h-2a.5.5 0 0 1-.5-.5z"/>
            </svg>
            </div>
            <div style={{position:'relative',top:'50vh',left:'10vh',fontFamily:'Bahnschrift Condensed',color:'aqua'}}>
                <h1 className='text-center'>
                your trust is our greatest asset
                </h1>
            </div>
        </div>


       
       
        </>
    )
}
export default Responsive;