// export default Email;

import React from 'react';
import { useForm, ValidationError } from '@formspree/react';
import { Button,Image } from 'react-bootstrap';
import Thanks from './thx';
import sendEmail from '../assets/sendEmail.jpg'
function ContactForm() {
  const [state, handleSubmit] = useForm("mrgwwqde");
  if (state.succeeded) {
      return <Thanks/>;
  }
  return (
    <div >
    <div style={{width: '70vh',position:'relative', left:'50vh',top:'30vh'}}>
      <form onSubmit={handleSubmit}>
      <div className="mb-3">
        <label htmlFor="exampleFormControlInput1" className="form-label">Email address</label>
        <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="name@example.com" name='exampleFormControlInput1'/>
        </div>
      <ValidationError 
        prefix="Email" 
        field="email"
        errors={state.errors}
      />
      <div class="mb-3">
        <label htmlFor="exampleFormControlTextarea1" className="form-label">Your Ask</label>
        <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" name='exampleFormControlTextarea1'></textarea>
        </div>
      <ValidationError 
        prefix="Message" 
        field="message"
        errors={state.errors}
      />
      <Button type='submit' disabled={state.submitting} variant='danger' >SEND</Button>
      
    </form>
    </div>
    <div  style={{position:'relative',width:'50vh',left:'130vh', top:'-15vh'}}>
        <Image src={sendEmail} fluid/>
    </div>
    </div>
    
  );
}
function Email() {
  return (
    <ContactForm />
  );
}
export default Email;